.sidebar{
    width: 260px;
    min-width: 260px;
}

.sidebar-card{
    margin-bottom: 10px;
    flex-direction: column;
    cursor: pointer;
}

.sidebar-card .header{
    display: flex;
    justify-content: space-between;
    padding: 10px;
}

.sidebar-card .card-content{
    padding: 10px;
}

.sidebar-card .card-content .active{
    font-weight: 800;
}